<template>
  <Card v-loading="loading">
    <template #header>
      <span class="font-bold text-black">Profile</span>
    </template>
    <template #default>
      <div class="grid grid-cols-2">
        <div class="xl:col-span-1 lg:col-span-1 col-span-2">
          <div class="grid grid-cols-2 gap-6">
            <div>
              <Input v-model="formData.first_name" label="First Name" :view="validation.first_name.$invalid && 'danger' || 'default'" block />
              <small class="mt-2 text-red-base block" v-for="(error, errorIndex) of validation.first_name.$errors" :key="errorIndex">{{ error.$message }}. </small>
            </div>
            <div>
              <Input v-model="formData.last_name" label="Last Name" :view="validation.last_name.$invalid && 'danger' || 'default'" block />
              <small class="mt-2 text-red-base block" v-for="(error, errorIndex) of validation.last_name.$errors" :key="errorIndex">{{ error.$message }}. </small>
            </div>
            <div class="col-span-2">
              <Input v-model="formData.email" label="Email" :view="validation.email.$invalid && 'danger' || 'default'" block />
              <small class="mt-2 text-red-base block" v-for="(error, errorIndex) of validation.email.$errors" :key="errorIndex">{{ error.$message }}. </small>
            </div>
          </div>
        </div>
      </div>
    </template>
    <template v-if="footer" #footer>
      <div class="flex justify-end">
        <Button @click="handleSubmit" color="teal" :loading="loading">Save</Button>
      </div>
    </template>
  </Card>
</template>

<script>
import { computed, defineComponent, watch } from 'vue'
import { useVuelidate } from '@vuelidate/core'
import { required, email } from '@/validations/index'

import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Input from 'bbUi/components/input/input.vue'

export default defineComponent({
  name: 'AccountSettingProfile',
  components: {
    Button,
    Card,
    Input
  },
  props: {
    data: {
      type: Object,
      default: () => ({})
    },
    loading: {
      type: Boolean,
      default: false
    },
    footer: {
      type: Boolean,
      default: true
    }
  },
  emits: ['submit', 'update:data'],
  setup(props, { emit }) {
    const formData = computed(() => props.data)
    
    watch(() => formData.value, (newValue) => {
      emit('update:data', newValue)
    }, {
      deep: true
    })

    const rules = {
      first_name: {
        required
      },
      last_name: {
        required
      },
      email: {
        required,
        email
      }
    }
     const validation = useVuelidate(rules, formData, {
      $lazy: true
    })

    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }

    const handleSubmit = () => {
      const isValid = validate()
      emit('submit', isValid)
    }

    return {
      formData,
      validation,
      validate,
      handleSubmit
    }
  }
})
</script>
