<template>
  <div>
    <div class="relative flex items-center justify-between mt-3 mb-8">
      <div class="flex items-center justify-between gap-x-2">
        <!-- <router-link :to="'/'">
          <i class="material-icons">file_download</i>
        </router-link> -->
        <h5>My Settings</h5>
      </div>
      <Button color="teal" @click="updatingData">
        Save Changes
      </Button>
    </div>
    <Profile
      ref="refProfile"
      v-model:data="dataProfile"
      :loading="loadingProfile"
      :footer="false"
      class="mb-4"
      @submit="handleProfileUpdate"
    />
    <TwoFactorAuth :loading="loadingProfile" />
  </div>
</template>

<script>
import { defineComponent, ref } from 'vue'
import { useProfile } from 'composable/user'

import { ElMessage } from 'element-plus'

import Button from 'bbUi/components/button/button.vue'
import Profile from '@/components/Account/Settings/Profile.vue'
import TwoFactorAuth from '@/components/Account/Settings/TwoFactorAuth.vue'

export default defineComponent({
  name: 'AccountSetting',
  components: {
    Button,
    Profile,
    TwoFactorAuth
  },
  async setup() {
    const refProfile = ref(null)
    const { loading: loadingProfile, data: dataProfile, action: actionProfile } = useProfile()
    
    const handleProfileUpdate = async (isValid=false) => {
      loadingProfile.value = true
      if(isValid){
        await actionProfile(() => {
          ElMessage({
            type: 'success',
            message: 'Profile updated'
          })
          loadingProfile.value = false
        })
      }
    }
    const updatingData = async () => {
      await refProfile?.value?.handleSubmit()
    }

    return {
      refProfile,
      dataProfile,
      loadingProfile,
      updatingData,
      handleProfileUpdate
    }
  }
})
</script>
