<template>
  <div>
    <Card v-loading="loading">
      <template #header>
        <span class="font-bold text-black">Security</span>
      </template>
      <template #default>
        <div class="grid grid-cols-2">
          <div class="xl:col-span-1 lg:col-span-1 col-span-2">
            <div class="flex items-start">
              <Checkbox v-model="me.dual_factor_authentication" class="mt-3" type="toggle" value="1" value-off="0" @click.prevent="handleOpenModal" />
              <div>
                <p @click="handleOpenModal" class="cursor-pointer font-semibold mb-2">Two-factor authentication</p>
                <small>Add an extra layer of security to your account by requiring a one-time SMS security code each time you sign into <span whitelabel="text-replace" data-label="addons host">Reusely.com</span>.</small>
              </div>
            </div>
          </div>
        </div>
      </template>
    </Card>
    <Modal title="Enable 2FA" v-model="state.twoFactorAuth.modal.activate" @close="handleCloseModal" size="xs">
      <template #default>
        <div class="flex flex-wrap flex-col items-center text-center mb-6">
          <img class="mb-6" src="@/assets/icon/verify-phone.svg" alt="verify phone number icon">
          <h6 class="mb-2">Verify Phone Number</h6>
          <p class="text-grey-04">Add your phone number to get a verification code sent to your phone</p>
        </div>
        <div class="flex items-end mb-4">
          <div class="mr-4 w-4/6">
            <InputPhone v-model="state.twoFactorAuth.data.phone_number" label="Phone" block country-autofill-setting />
          </div>
          <div class="w-2/6">
            <Button @click="handleSendCode" color="teal" view="smooth" :loading="state.twoFactorAuth.loading.phone_number_send" block :disabled="isDisableSend || !isEnableResendCode" >{{isVerificationSend ? (!isEnableResendCode ? `Resend (${resendCountdownTimer})` : 'Resend') : 'Send Code'}}</Button>
          </div>  
        </div>
        <span v-if="isVerificationSend" class="block text-green-04 mb-2">The verification code is being sent to {{phoneNumber(state.twoFactorAuth.last_saved_phone_number)}}</span>
        <Input v-model="state.twoFactorAuth.data.phone_code" v-maska="'######'" label="Enter Your Verification Code" block :disabled="!state.twoFactorAuth.is_send" />
      </template>
      <template #footer>
        <div class="flex flex-wrap items-center justify-between">
          <a class="text-red-04 hover:text-red-03 select-none" @click.prevent="handleCloseModal">Cancel</a>
          <Button @click="handleVerifyCode" :disabled="isDisableVerify" :loading="state.twoFactorAuth.loading.verify_code" color="teal" view="smooth" dense>Set Up</Button>
        </div>
      </template>
    </Modal>
    <Modal v-model="state.twoFactorAuth.modal.deactivate" hide-footer size="xs">
      <template #default>
        <div class="flex flex-wrap flex-col text-center justify-center">
          <img class="mb-12" src="@/assets/icon/attention-warning.svg" alt="warning icon">
          <h6 class="mb-4">Disable 2FA from your account?</h6>
          <p class="text-grey-04">This will disable Two-Factor Authentication from your account. <br/> Are you sure you want to continue?</p>
          <div class="my-10">
            <Button @click="handleDisableTwoFactorAuth" color="teal" view="outlined" :loading="state.twoFactorAuth.loading.deactivate">Yes, Disable</Button>
          </div>
        </div>
      </template>
    </Modal>
  </div>
</template>

<script>
import { computed, defineComponent } from 'vue'
import { state, useTwoFactorAuth, useUser } from 'composable/user'

import { useVuelidate } from '@vuelidate/core'
import { helpers } from '@vuelidate/validators'
import { required } from '@/validations/index'
import { phoneNumber } from 'utils/number'

import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Input from 'bbUi/components/input/input.vue'

import InputPhone from 'components/InputPhone.vue'
import { ElMessage } from 'element-plus'

export default defineComponent({
  name: 'AccountSettingTwoFactorAuth',
  components: {
    Button,
    Card,
    Checkbox,
    Modal,
    Input,
    InputPhone
  },
  props: {
    loading: {
      type: Boolean,
      default: false
    }
  },
  setup() {
    const { resendCountdownTimer, setResendCountdownTimer, openModal, openModalDeactivate, closeModal: handleCloseModal, actionSend, actionVerifyCode, actionDeactivate } = useTwoFactorAuth()
    const { me } = useUser()
    
    const isVerificationSend = computed(() => state.twoFactorAuth.is_send)
    const isDisableSend = computed(() => !state.twoFactorAuth.data.phone_number)
    const isEnableResendCode = computed(() => resendCountdownTimer.value === 0)
    const isDisableVerify = computed(() => !state.twoFactorAuth.data.phone_code || !state.twoFactorAuth.is_send)
    const isActiveTwoFactorAuth = computed(() => me.value.dual_factor_authentication == 1)
    
    const rules = {
      phone_number: {
        required
      },
      phone_code: {
        required: helpers.withMessage('Verification code is required', () =>  state.twoFactorAuth.is_send == 0 ? true : state.twoFactorAuth.is_send == 1 && !!state.twoFactorAuth.data.phone_code)
      }
    }
     const validation = useVuelidate(rules, state.twoFactorAuth.data, {
      $lazy: true
    })

    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }
    const handleSendCode = async () => {
      state.twoFactorAuth.is_send = false
      const isValid = validate()
      if(isValid) await actionSend(() => {
        setResendCountdownTimer(60)
      })
    }
    const handleVerifyCode = async () => {
      const isValid = validate()
      if(isValid) await actionVerifyCode(() => {
        ElMessage({
          type: 'success',
          message: '2-Factor Authentication enabled'
        })
      })
    }
    const handleOpenModal = () => {
      if(!isActiveTwoFactorAuth.value) {
        return openModal()
      }
      openModalDeactivate()
    }
    const handleDisableTwoFactorAuth = async () => {
      state.twoFactorAuth.data = {
        phone_number: '',
        phone_code: ''
      }
      await actionDeactivate(() => {
        ElMessage({
          type: 'success',
          message: '2-Factor Authentication disabled'
        })
      })
    }

    return {
      me,
      state,
      resendCountdownTimer,
      isVerificationSend,
      isDisableSend,
      isDisableVerify,
      isEnableResendCode,
      validation,
      validate,
      handleOpenModal,
      handleCloseModal,
      handleSendCode,
      handleVerifyCode,
      handleDisableTwoFactorAuth,
      phoneNumber
    }
  }
})
</script>
